<template>
  <div class="MailTemplateList-box" v-cloak>
    <!---->
    <div class="add_btn">
      <div class="buttons"> <el-button type="primary" @click="initModal" size="small"></el-button></div>

      <el-table v-loading="zLoading" element-loading-text="" :data="mailData" :height="tHeight" stripe style="width: 100%;" empty-text=" " row-key="id">
        <el-table-column type="index" fixed="left" width="70" label="" :index="typeIndex"></el-table-column>
        <el-table-column fixed="left" prop="title" label="" width="500" show-overflow-tooltip></el-table-column>
        <el-table-column prop="body"  fit show-overflow-tooltip label="">
        </el-table-column>
        <el-table-column fixed="right" label="" width="160" align="center">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click.native.prevent="editMailTemplate(scope.row,scope.$index)"></el-button>
            <el-button type="text" size="small" @click.native.prevent="deleteMailTemplate(scope.row,scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <div style="margin-top: 10px; height: 32px; line-height: 32px; text-align: center;">
        <span style="float: left; text-align: right; color: #606266; font-size: 14px; padding-top: 3px;"> {{ zPager.total }} </span>
        <el-pagination layout="prev, pager, next" :page-size="zPager.size" :pager-count="zPager.count" :current-page.sync="zPager.currentPage" :total="zPager.total" @current-change="pagerChange">
        </el-pagination>
      </div>
      <div>
        <el-dialog :title="isEditDialog == 1? '' : ''" :visible.sync="addMail.centerDialogVisible"  center>
          <el-form :model="addMail.addMailTemplate" :rules="addMail.rules" ref="addMail.addMailTemplate" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="title">
              <el-input v-model="addMail.addMailTemplate.title"></el-input>
            </el-form-item>
              <el-form-item label="" prop="body">
              <li-ueditor ref="ueditor" :default-value='addMail.addMailTemplate.body'></li-ueditor>
            </el-form-item>
            <el-form-item class="form_btn">
              <el-button @click="addMail.centerDialogVisible = false"  size="small"> </el-button>
              <el-button type="primary" @click="isValid('addMail.addMailTemplate')"  size="small"> </el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </div>
    </div>
    <!---->
    </div>
  </template>
<script>
import ueditor from '../commen/Ueditor.vue'
export default {
  name: 'MailTemplateList',
  data () {
    return {
      tHeight: document.documentElement.clientHeight - 82,
      zLoading: true,
      mailData: [],
      zPager: {
        total: 0,
        size: 30,
        count: 11,
        currentPage: 1
      },
      addMail: {
        centerDialogVisible: false,
        addMailTemplate: {
          title: '',
          body: '',
          id: ''
        },
        rules: {
          title: [
            {
              required: true,
              message: ''
            }
          ],
          body: [
            {
              required: true,
              message: ''
            }
          ]
        }
      },
      isEditDialog: 1, // 12
      clickedIdx: 0 // 
    }
  },
  components: {
    'li-ueditor': ueditor
  },
  props: ['defaultValue'],
  methods: {
    typeIndex (index) {
      return index + (this.zPager.currentPage - 1) * this.zPager.size + 1
    },
    // 
    getMail () {
      var that = this
      that.zLoading = true
      var apiPath = that.apiPath + 'MailTemplate'
      that.$ajax
        .get(apiPath)
        .then(function (response) {
          let res = response.data
          if (res.Code === 1000) {
            that.mailData = res.Result.Data
            that.zPager.total = res.Result.Total
          }
          that.zLoading = false
        })
        .catch(function (response) {})
    },
    // 
    initModal () {
      this.addMail.centerDialogVisible = true
      this.isEditDialog = 1
      this.addMail.addMailTemplate.title = ''
      this.addMail.addMailTemplate.body = ''
      if (this.$refs.ueditor) {
        this.$refs.ueditor.setUEContent('')
      }
    },
    // 
    isValid (formName) {
      var addEmailParam = this.addMail.addMailTemplate
      // ueditor
      addEmailParam.body = this.$refs.ueditor.getUEContent()
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.addMailTemplate()
        } else {

        }
      })
    },
    // 
    addMailTemplate (formName) {
      var that = this
      var addEmailParam = {
        title: this.addMail.addMailTemplate.title,
        body: this.addMail.addMailTemplate.body,
        id: this.addMail.addMailTemplate.id
      }
      this.addMail.centerDialogVisible = false
      var apiPath = that.apiPath + 'MailTemplate'
      if (this.isEditDialog === 1) {
        // 
        delete addEmailParam.id
        that.$ajax
          .post(apiPath, addEmailParam)
          .then(function (response) {
            let res = response.data
            if (res.Code === 1000) {
              addEmailParam.id = res.Result.Data
              // 
              that.mailData.unshift(addEmailParam)
              that.$message({
                message: '',
                type: 'success'
              })
            }
            that.zLoading = false
          })
          .catch(function (response) {})
      } else if (this.isEditDialog === 2) {
        that.$ajax
          .put(apiPath, addEmailParam)
          .then(function (response) {
            let res = response.data
            if (res.Code === 1000) {
              // 
              that.mailData[that.clickedIdx].title = addEmailParam.title
              that.mailData[that.clickedIdx].body = addEmailParam.body
              that.$message({
                message: '',
                type: 'success'
              })
            }
            that.zLoading = false
          })
          .catch(function (response) {})
      }
    },
    // 
    editMailTemplate (param, index) {
      this.clickedIdx = index
      this.isEditDialog = 2
      this.addMail.centerDialogVisible = true
      this.addMail.addMailTemplate.title = param.title
      if (this.$refs.ueditor) {
        this.$refs.ueditor.setUEContent(param.body)
      } else {
        this.addMail.addMailTemplate.body = param.body // 
      }

      this.addMail.addMailTemplate.id = param.id
    },
    // 
    deleteMailTemplate (row, id) {
      const that = this
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          var apiPath =
            that.apiPath + 'MailTemplate/' + id
          that.$ajax
            .delete(apiPath)
            .then(function (response) {
              let res = response.data
              if (res.Code === 1000) {
                // 
                that.mailData = that.mailData.filter(a => a['id'] !== id)
                // 
                that.zPager.total--
                // 
                that.$message({
                  message: '',
                  type: 'success'
                })
              }
              that.zLoading = false
            })
            .catch(function (response) {})
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '...'
          })
        })
    },
    pagerChange (val) {
      console.log(val)
    }
  },
  created () {
    this.getMail()
  },
  mounted () {
    const that = this
    window.onresize = () => {
      return (() => {
        that.tHeight = document.documentElement.clientHeight - 82
      })()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
[v-cloak] {
  display: none;
}

html,
body {
  padding: 0;
  margin: 0;
  font-size: 14px;
}

.add_btn>.buttons{
  width: 100%;
text-align: right;
}
.mail-textarea {
  margin-top: 20px;
}

.form_btn {
  margin-right: 100px !important;
  margin-bottom: 0px !important;
  text-align: center;
}
</style>



