<template>
  <div>
    <div>
      <el-table ref="singleTable" :data="tableData" highlight-current-row   style="width: 100%">
        <el-table-column  type="index" label="" width="50"></el-table-column>
        <el-table-column label=""  min-width="350" fit show-overflow-tooltip>
                <template slot-scope="scope">
                    <span style="cursor:pointer">{{scope.row.lawName}}</span>
                    <span class="blue">{{scope.row.count==0?'()':('('+scope.row.count+')')}}</span>
                </template>
        </el-table-column>
        <el-table-column property="published" label=""></el-table-column>
        <el-table-column property="typeName" label=""> </el-table-column>
        <el-table-column property="sourceDepartment" label=""> </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-rate v-model="scope.row.lawGrade" disabled :show-score="false"></el-rate>
          </template>
       </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
export default {
  name: 'infoLaw',
  data () {
    return {
      tableData: [{
        count: 1,
        lawGrade: 3,
        lawId: '746126805380594909',
        lawInvalid: '',
        lawItemList: null,
        lawName: '19(2018)',
        lawStatus: '0',
        nodeId: null,
        published: '2018-04-24',
        sourceDepartment: '',
        typeId: '24181902543261879',
        typeName: ''
      }]
    }
  }
}
</script>
<style scoped>
.blue{
  color: #409EFF;
  cursor: pointer;
}
</style>



