<template>
  <div class="MsgTemplateList-box" v-cloak>
    <!---->
    <div class="add_btn">
      <div class="buttons"> <el-button type="primary" @click="initModal" size="small"></el-button></div>

      <el-table v-loading="zLoading" element-loading-text="" :data="mailData" :height="tHeight" stripe style="width: 100%;" empty-text=" " row-key="id">
        <el-table-column type="index" fixed="left" width="70" label="" :index="typeIndex"></el-table-column>
        <el-table-column prop="id" fixed="left" width="350" label="ID"></el-table-column>
        <el-table-column prop="content" fit show-overflow-tooltip label=""></el-table-column>
        <el-table-column fixed="right" label="" width="160" align="center">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click.native.prevent="editMsgTemplate(scope.row,scope.$index)"></el-button>
            <el-button type="text" size="small" @click.native.prevent="deleteMsgTemplate(scope.row,scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <div style="margin-top: 10px; height: 32px; line-height: 32px; text-align: center;">
        <span style="float: left; text-align: right; color: #606266; font-size: 14px; padding-top: 3px;"> {{ zPager.total }} </span>
        <el-pagination layout="prev, pager, next" :page-size="zPager.size" :pager-count="zPager.count" :current-page.sync="zPager.currentPage" :total="zPager.total" @current-change="pagerChange">
        </el-pagination>
      </div>
      <div>
        <el-dialog :title="isEditDialog == 1? '' : ''" :visible.sync="addMail.centerDialogVisible"  center>
          <el-form :model="addMail.msg_model" :rules="addMail.rules" ref="addMail.msg_model" label-width="100px" class="demo-ruleForm">
            <el-form-item label="" prop="Content">
              <textarea name="Content" id="Content" cols="30" rows="10" v-model="addMail.msg_model.Content"></textarea>
            </el-form-item>
            <el-form-item class="form_btn">
              <el-button @click="addMail.centerDialogVisible = false"  size="small"> </el-button>
              <el-button type="primary" @click="isValid('addMail.msg_model')"  size="small"> </el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </div>
    </div>
    <!---->
    </div>
  </template>
<script>
import ueditor from '../commen/Ueditor.vue'
export default {
  name: 'MsgTemplateList',
  data () {
    return {
      tHeight: document.documentElement.clientHeight - 82,
      zLoading: true,
      mailData: [],
      zPager: {
        total: 0,
        size: 30,
        count: 11,
        currentPage: 1
      },
      addMail: {
        centerDialogVisible: false,
        msg_model: {
          Content: '',
          ID: ''
        },
        rules: {
          msg_moDel: [
            {
              required: true,
              message: ''
            }
          ]
        }
      },
      isEditDialog: 1, // 12
      clickedIdx: 0 // 
    }
  },
  components: {
    'li-ueditor': ueditor
  },
  props: ['defaultValue'],
  methods: {
    typeIndex (index) {
      return index + (this.zPager.currentPage - 1) * this.zPager.size + 1
    },
    // 
    getMail () {
      var that = this
      that.zLoading = true
      var apiPath = that.apiPath + '/MessageTemplate'
      that.$ajax
        .get(apiPath)
        .then(function (response) {
          let res = response.data
          if (res.Code === 1000) {
            that.mailData = res.Result.Data
            that.zPager.total = res.Result.Total
          }
          that.zLoading = false
        })
        .catch(function (response) {})
    },
    // 
    initModal () {
      this.addMail.centerDialogVisible = true
      this.isEditDialog = 1
      this.addMail.msg_model.Content = ''
    },
    // 
    isValid (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.addMailTemplate()
        }
      })
    },
    // 
    addMailTemplate (formName) {
      var that = this
      var addEmailParam = {
        Content: this.addMail.msg_model.Content,
        ID: this.addMail.msg_model.ID
      }
      this.addMail.centerDialogVisible = false
      var apiPath = that.apiPath + 'MessageTemplate'
      if (this.isEditDialog === 1) { // 
        delete addEmailParam.ID
        that.$ajax
          .post(apiPath, that.addMail.msg_model)
          .then(function (response) {
            let res = response.data
            if (res.Code === 1000) {
              addEmailParam.Id = res.Result.Data
              // 
              that.mailData.unshift({'content': that.addMail.msg_model.Content, 'id': addEmailParam.Id})
              that.$message({
                message: '',
                type: 'success'
              })
            }
            that.zLoading = false
          })
          .catch(function (response) {})
      } else if (this.isEditDialog === 2) { // 
        that.$ajax
          .put(apiPath, that.addMail.msg_model)
          .then(function (response) {
            let res = response.data
            if (res.Code === 1000) {
              // 
              that.mailData[that.clickedIdx].content = addEmailParam.Content
              that.$message({
                message: '',
                type: 'success'
              })
            }
            that.zLoading = false
          })
          .catch(function (response) {})
      }
    },
    // 
    editMsgTemplate (param, index) {
      this.clickedIdx = index
      this.isEditDialog = 2
      this.addMail.centerDialogVisible = true
      this.addMail.msg_model.Content = param.content
      this.addMail.msg_model.ID = param.id
    },
    // 
    deleteMsgTemplate (row, id) {
      const that = this
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          var apiPath =
            that.apiPath + 'MessageTemplate/' + id
          that.$ajax
            .delete(apiPath)
            .then(function (response) {
              let res = response.data
              if (res.Code === 1000) {
                // 
                that.mailData = that.mailData.filter(a => a['id'] !== id)
                // 
                that.zPager.total--
                // 
                that.$message({
                  message: '',
                  type: 'success'
                })
              }
              that.zLoading = false
            })
            .catch(function (response) {})
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '...'
          })
        })
    },
    pagerChange (val) {
      this.getMail()
    }
  },
  created () {
    this.getMail()
  },
  mounted () {
    const that = this
    window.onresize = () => {
      return (() => {
        that.tHeight = document.documentElement.clientHeight - 82
      })()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
[v-cloak] {
  display: none;
}
textarea{
  width: 100%;
  border: 1px solid #ccc;
}
html,
body {
  padding: 0;
  margin: 0;
  font-size: 14px;
}
.add_btn>.buttons{
  width: 100%;
text-align: right;
}
.mail-textarea {
  margin-top: 20px;
}

.form_btn {
  margin-right: 100px !important;
  margin-bottom: 0px !important;
  text-align: center;
}
</style>



