      <el-table-column property="type" label=""> </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: 'infoFile',
  data () {
    return {
      tableData: [{
        file: '',
        newspaper: '',
        net: '',
        Preparation: '',
        neccessary: '',
        type: ''
      }]
    }
  }
}
</script>
